<template>
  <div class="container bg-container">
    <Header :title="t('index.我的團隊')" type="back"></Header>
    <div class="net border-out">
      <div class="border-in">
        <div class="level-wrap">
          <div class="tab">
            <!-- <div class="tab-ink-bar">
            <span class="tab-inner"></span>
          </div> -->
            <div class="tab-item">{{t('index.左區')}}({{leftpv}}U)</div>
            <div class="tab-item">{{t('index.右區')}}({{rightpv}}U)</div>
          </div>
          <div class="tab-content border-out">
            <div class="border-in">
              <div class="theader flex-middle flex-between">
                <div class="td">{{t('index.層级关系')}}</div>
                <div class="td">{{t('index.推荐奖励')}}</div>
              </div>
              <div class="tbody">
                <div class="tr" v-for="(item, index) in ceng" :key="index">
                  <div class="td">{{index}}</div>
                  <div class="td">{{item}}USDT</div>
                </div>
                <div class="tr tfooter">
                  <div class="td">{{t('index.合計')}}</div>
                  <div class="td">{{tjjtotal}} USDT</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="intro-wrap border-out">
          <div class="border-in">
            <div class="title">{{t('index.我的推薦')}}</div>
            <div class="theader">
              <div class="td">{{t('index.序號')}}</div>
              <div class="td">{{t('index.錢包地址')}}</div>
              <div class="td">{{t('index.時間')}}</div>
            </div>
            <div class="tbody">
              <div class="tr" v-for="(item,index) in recList" :key="index">
                <div class="td">{{index+1}}</div>
                <div class="td">{{item.address.substring(0, 5) + '...' + item.address.substring(item.address.length - 5, item.address.length)}}</div>
                <div class="td">{{item.time}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getNet } from "@/api"
export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts),
      web3: computed(() => store.state.provider),
      balance: 0,
      categoryList: [
        {
          name: t('index.左區'),
          id: 0
        }, {
          name: t('index.右區'),
          id: 1
        }
      ],
      cid: 0,
      recList: [],
      ceng: [],
      leftpv: 0,
      rightpv: 0,
      tjjtotal: 0,
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.getNet()
      },
      getNet: () => {
        getNet({ address: data.address }).then((res) => {
          data.recList = res.data.myrec
          data.ceng = res.data.ceng
          data.leftpv = res.data.leftpv
          data.rightpv = res.data.rightpv
          data.tjjtotal = res.data.tjjtotal
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.tab {
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
  flex-wrap: nowrap;
  overflow-x: auto;
  padding: 0 15px;
  &::-webkit-scrollbar {
    display: none;
  }

  .tab-item {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    color: #000000;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    flex-shrink: 0;
    width: 50%;

    &.active {
      font-size: 17px;
      color: #fff;
    }
  }

  .tab-ink-bar {
    position: absolute;
    left: 0;
    bottom: 12px;
    width: 22px;
    height: 4px;
    background-color: transparent;
    transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s,
      left 0.3s cubic-bezier(0.35, 0, 0.25, 1);

    .tab-inner {
      border-radius: 0;
      display: block;
      margin: auto;
      height: 100%;
      background: linear-gradient(130deg, #447af4 25%, #33e7f7 100%);
      border-radius: 5px;
    }
  }
}
.net {
  border-radius: 20px 20px 0 0;
  padding: 1px;
  background-image: linear-gradient(0deg, #000 0%, #9b9a9a 100%);
  & > .border-in {
    background: #07132b;
    border-radius: 20px;
    padding-bottom: 17px;
  }
  .level-wrap {
    margin-bottom: 20px;
    .tab-content {
      font-size: 14px;
      margin: 0 15px;
      border-radius: 15px;
      overflow: hidden;

      .theader {
        background: #1c2c4b;
        border-radius: 15px 15px 0 0;
        padding: 0 16px;
        min-height: 44px;
      }
      .tbody {
        .tr {
          display: flex;
          padding: 0 20px 0 16px;
        }
        .tfooter {
          border-radius: 15px;
          background: linear-gradient(90deg, #074aa3, #0188d9);
        }
        .td {
          min-height: 44px;
          display: flex;
          align-items: center;
          &:nth-child(1) {
            flex-shrink: 0;
          }
          &:nth-child(2) {
            flex-grow: 1;
            justify-content: flex-end;
          }
        }
      }
    }
  }
  .intro-wrap {
    border-radius: 15px;
    margin: 0 16px 20px;
    .title {
      font-size: 18px;
      font-weight: 600;
      padding: 0 16px;
      height: 50px;
      line-height: 50px;
    }
    .theader {
      border-radius: 20px 20px 0 0;
      padding: 0 16px;
      color: rgba(255, 255, 255, 0.7);
      display: flex;
      align-items: center;
      .td {
        height: 44px;
        line-height: 44px;
        font-size: 14px;
        &:nth-child(1) {
          text-align: left;
          width: 17%;
        }
        &:nth-child(2) {
          text-align: center;
          width: 41%;
        }
        &:nth-child(3) {
          text-align: right;
          width: 44%;
        }
      }
    }
    .tbody {
      .tr {
        display: flex;
        align-items: center;
        padding: 0 16px;
        .td {
          height: 44px;
          line-height: 44px;
          font-size: 14px;
          &:nth-child(1) {
            text-align: left;
            width: 17%;
          }
          &:nth-child(2) {
            text-align: center;
            width: 41%;
          }
          &:nth-child(3) {
            text-align: right;
            width: 44%;
          }
        }
      }
    }
  }
}
</style>
